var _self;
import share from '@/components/share';
export default{
	components: {
		share
	},
	data() {
		return {
			specClass: 'none',
			specSelected:[],
			goods:[],
			favorite: true,
			shareList: [],
			specList: [
				{
					id: 1,
					name: '尺寸',
				},
				{	
					id: 2,
					name: '颜色',
				},
			],
			specChildList: [
				{
					id: 1,
					pid: 1,
					name: 'XS',
				},
				{
					id: 2,
					pid: 1,
					name: 'S',
				},
				{
					id: 3,
					pid: 1,
					name: 'M',
				},
				{
					id: 4,
					pid: 1,
					name: 'L',
				},
				{
					id: 5,
					pid: 1,
					name: 'XL',
				},
				{
					id: 6,
					pid: 1,
					name: 'XXL',
				},
				{
					id: 7,
					pid: 2,
					name: '白色',
				},
				{
					id: 8,
					pid: 2,
					name: '珊瑚粉',
				},
				{
					id: 9,
					pid: 2,
					name: '草木绿',
				},
			]
		};
	},
	onLoad(options){
		_self = this;
		_self.loadData(options.id); //optiosn就是goods_id
		// //规格 默认选中第一条
		// this.specList.forEach(item=>{
		// 	for(let cItem of this.specChildList){
		// 		if(cItem.pid === item.id){
		// 			this.$set(cItem, 'selected', true);
		// 			this.specSelected.push(cItem);
		// 			break; //forEach不能使用break
		// 		}
		// 	}
		// })
		//this.shareList = await this.$api.json('shareList');
	},
	methods:{
		//加载数据
		async loadData(g_id){
			let goods = await this.$apiRequest({
				url:'goods/'+ g_id
			});
			_self.goods = goods.data.data;
			//console.log(_self.goods);
			// _self.imgList = goods.goods_gallery;
			// _self.desc = goods.goods_desc;
		},
		//规格弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(index, pid){
			let list = this.specChildList;
			list.forEach(item=>{
				if(item.pid === pid){
					this.$set(item, 'selected', false);
				}
			})

			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = []; 
			list.forEach(item=>{ 
				if(item.selected === true){ 
					this.specSelected.push(item); 
				} 
			})
			
		},
		//分享
		share(){
			this.$refs.share.toggleMask();	
		},
		//收藏
		toFavorite(){
			this.favorite = !this.favorite;
		},
		buy(){
			//立即购买
			uni.navigateTo({
				url: `/pages/order/createOrder`
			})
		},
		addCart(){
			//加入购物车
			let cartList = uni.getStorageSync('carts');
			let meiyou = true;
			if (cartList.length > 0)
			{
				cartList.forEach(item=>{
					if (item.id == _self.goods.goods_id)
					{
						item.number ++;
						_self.$api.msg('购物车数量增加');
						uni.setStorageSync('carts',cartList);
						meiyou = false;
						return false;
					}
				});
			} else {
				cartList = [];
			}
			
			if (meiyou) {
				_self.$api.msg('没有，现在增加');
				cartList = cartList.concat({
					id: _self.goods.goods_id,
					image: _self.goods.goods_thumb,
					attr_val: '',
					stock: _self.goods.goods_number,
					title: _self.goods.goods_name,
					price: _self.goods.shop_price,
					number:1,
				});
				_self.$api.msg('购物车增加新产品');
				uni.setStorageSync('carts',cartList);
			}
			
		},
		stopPrevent(){}
	},

}
